import i18n from 'i18next'
import LanguageDetector from 'i18next-browser-languagedetector'

i18n.use(LanguageDetector).init({
    resources: {
        en: {
            translations: {
                'home.title': 'Time is money',
                'home.label': 'Your net monthly income',
                'home.placeholder': 'Net income',
                'home.btn': 'Start counters',
                'counter.message': 'You have earned this year',
                'counter.message-month': 'Month: {{salary}}',
                'counter.message-week': 'This week: {{salary}}',
                'counter.message-today': 'Today: {{salary}}',
                'counter.message-hour': 'This hour: {{salary}}',
                'counter.share-link': 'Save this counter or share with your friend:'
            }
        },
        ru: {
            translations: {
                'home.title': ' - !',
                'home.label': '   ',
                'home.placeholder': ' ',
                'home.btn': ' ',
                'counter.message': '    ',
                'counter.message-month': ' : {{salary}}',
                'counter.message-week': ' : {{salary}}',
                'counter.message-today': ': {{salary}}',
                'counter.message-hour': ' : {{salary}}',
                'counter.share-link': '      :'
            }
        }
    },
    fallbackLng: 'en',
    debug: true,

    ns: ['translations'],
    defaultNS: 'translations',

    keySeparator: false,

    interpolation: {
        escapeValue: false,
        formatSeparator: ','
    },

    react: {
        wait: true
    },

    detection: {
        order: ['navigator', 'querystring', 'cookie', 'localStorage', 'htmlTag', 'path', 'subdomain']
    }
})

export default i18n



